.nav__toggle {
  display: block;
  width: 24px;
  height: 18px;
  margin-right: 20px;
  position: relative;
  z-index: 5;
}
.nav__toggle .nav__line {
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.nav__toggle .nav__line--first {
  top: 0;
  transition: 1s;
}
.nav__toggle .nav__line--second {
  left: 0;
  transition: 1s;
}
.nav__toggle .nav__line--last {
  top: auto;
  bottom: 0;
  transition: 1s;
}
@media (min-width: 768px) {
  .nav__toggle .nav__line {
    display: none;
  }
}

.nav__toggle.active .nav__line {
  transform: scale(0);
}
.nav__toggle.active .nav__line--first {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
  transition: 1s;
}
.nav__toggle.active .nav__line--second {
  left: 100%;
  transition: 1s;
}
.nav__toggle.active .nav__line--last {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
  transition: 1s;
}

