:root {
  --main-font: "Oswald", sans-serif;
  --additional-font: "Arial", "Helvetica", sans-serif;
  --primary-color: #000000;
  --font-color-light: #ffffff;
  --bg-color-light: #ffffff;
  --font-color-dark: #444444;
  --bright-color: #68b738;
  --backgroun-main-color: #f2f2f2;
  --border-color: #ebebeb;
  --content-width: 1220px;
  --magic-indent: calc(50% - var(--content-width) / 2 - 40px);
}

.page__body {
  min-width: 320px;
  box-sizing: border-box;
}

.copyright {
  display: flex;
  align-items: center;
  font-family: var(--additional-font);
  font-size: 16px;
  line-height: 1.2;
  text-decoration: none;
  color: var(--font-color-dark);
}

.copyright:active {
  opacity: 30%;
}

.copyright__img {
  fill: #666666;
}

.copyright:hover .copyright__img {
  fill: #68b738;
}

@media (min-width: 768px) {
  .copyright__text {
    margin-right: 22px;
  }
}

.slider {
  grid-column: 2/3;
  grid-row: 1/5;
  position: relative;
  width: 280px;
  height: 256px;
  display: grid;
  grid-template-columns: var(--percent) 1fr;
  padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  --percent: 50%;
}
@media (min-width: 768px) {
  .slider {
    width: 560px;
    height: 512px;
  }
}
@media (min-width: 1440px) {
  .slider {
    margin: 20px 0 0 0;
  }
}

.slider-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__image-before {
  object-position: left;
}

.slider__image-after {
  object-position: right;
}

.slider__button {
  position: absolute;
  top: 42%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  border: none;
  background: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .slider__button {
    top: 46%;
  }
}

.slider__button::before {
  content: "";
  position: absolute;
  width: 4px;
  height: 256px;
  left: 50%;
  background-color: #666666;
  z-index: -1;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .slider__button::before {
    height: 512px;
  }
}

.slider__button:hover::before {
  background-color: #68b738;
}

.slider__button:active::before {
  opacity: 0.6;
}

.slider__svg {
  stroke: #666666;
  fill: #666666;
}

.slider__button:hover .slider__svg {
  stroke: #68b738;
  fill: #68b738;
}

.slider__button:active .slider__svg {
  opacity: 0.6;
}

.page {
  height: 100%;
}

.page__body {
  height: 100%;
  margin: 0;
  box-sizing: border-box;
  position: relative;
}